.popup-places__container {
  width: 100%;
  max-width: 430px;
  background-color: #ffffff;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
}

@media screen and (max-width: 680px) {
  .popup-places__container {
    max-width: 282px;
    padding-bottom: 0;
  }
}
