.profile__subtitle {
  margin: 0;
  max-width: 250px;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  grid-area: subtitle;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 680px) {
  .profile__subtitle {
    margin: 0 auto;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }
}